@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Lato", sans-serif}h1,h2,h3{color:#383838}.song-container{min-height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.song-container img{width:15rem;height:15rem;border-radius:50%;border:0.5rem solid #bcebe3;object-fit:cover;object-position:center}@keyframes infinite-rotate{0%{-webkit-transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg)}}.song-container h2{font-family:"Satisfy", cursive;font-size:2rem;padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}.img-rotate{animation:infinite-rotate 60s linear infinite}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.time-control{width:50%;display:flex;align-items:center;justify-content:center}.time-control input{width:100%;min-width:15rem}.time-control p{padding:1rem}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:25%;min-width:15rem}.play-control svg{cursor:pointer}.library{position:fixed;top:0;left:0;width:20rem;height:100%;background-color:white;box-shadow:2px 2px 50px #cacaca;overflow-y:scroll;transform:translateX(-100%);opacity:0;transition:all 0.5s ease}.library h2{text-align:center;padding:2rem}.library-song{display:flex;align-items:center;padding:1rem 2rem;cursor:pointer}.library-song img{width:5rem;height:5rem;border-radius:10px;object-fit:cover;object-position:center}.library-song:hover{background-color:#dbebff}.song-description{padding-left:1rem}.song-description h3{font-size:1rem;font-weight:700}.song-description h4{font-size:0.8rem;font-weight:400}*{scrollbar-width:thin;scrollbar-color:rgba(150,150,150,0.5)}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(150,150,150,0.5);border-radius:20px;border:transparent}.selected{background-color:#bed5ff}.active-library{transform:translateX(0);opacity:1}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;padding:0.5em 0.7em;border:2px solid #464646;cursor:pointer;transition:all 0.3s ease}nav button:hover{background:#464646;color:white}

